<template>
  <div class="login-container">
    <el-form ref="loginForm" :model="loginForm" class="login-form" auto-complete="on" label-position="left">

      <div class="title-container">
        <h3 class="title">后台管理系统</h3>
      </div>

      <el-form-item prop="username">
        <span class="svg-container">
          <i class="el-icon-user-solid" />
        </span>
        <el-input
          ref="username"
          v-model="loginForm.username"
          placeholder="Username"
          name="username"
          type="text"
          tabindex="1"
          auto-complete="on"
        />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <i class="el-icon-lock" />
        </span>
        <el-input
          :key="passwordType"
          ref="password"
          v-model="loginForm.password"
          :type="passwordType"
          placeholder="Password"
          name="password"
          tabindex="2"
          auto-complete="on"
          @keyup.enter.native="handleLogin"
        />
        <span class="show-pwd" @click="showPwd">
          <i class="el-icon-view" />
        </span>
      </el-form-item>

      <el-button :loading="loading" type="primary" style="width:100%;margin-bottom:30px;" @click.native.prevent="handleLogin">Login</el-button>

    </el-form>
  </div>
</template>

<script>

export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loading: false,
      passwordType: 'password',
      redirect: undefined
    }
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  mounted() {
    if(window.sessionStorage.getItem('satoken') !== null || window.sessionStorage.getItem('satoken') !== undefined){
      this.getRequest('/user/login/check').then(resp => {
        if(resp){
          if(resp.data === false){
            window.sessionStorage.removeItem('satoken');
            this.$router.replace('/login');
          }else{
            this.$router.replace('/dashboard');
            this.$message.success('登陆成功！');
          }
        }
      })
    }
  },
  methods: {
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleLogin: function() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true
          this.postRequest('/admin/login', this.loginForm).then(resp => {
            if (resp) {
              console.log(resp)
              const token = resp.data.token
              const userInfo = resp.data.user
              window.sessionStorage.setItem('satoken', token)
              window.sessionStorage.setItem('user', JSON.stringify(userInfo))
              const path = this.$route.query.redirect
              this.$router.replace((path === '/' || path === undefined) ? '/dashboard' : path)
            }
            setInterval(() => {
              this.loading = false
            }, 1500)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

